import {
    Button,
    ModalPage,
    ModalPageHeader,
    ModalRoot,
    FormLayout,
    FormItem, Input, IconButton, Header, Title, PanelHeaderBack
} from "@vkontakte/vkui";
import {useState} from "react";
import {Icon24Add, Icon24Cancel} from "@vkontakte/icons";
import fs from "../Functions/Firebase";

const Modal = (p) => {
    const [type, setType] = useState(null);
    const [input, updateInput] = useState(0);
    const formatInput = (t) => {
        t = t.replace(/[^0-9]/g, '');
        if(t.charAt(0) === '0' && t > 0){
            t = t.substr(1);
        }
        if(t.length === 0){
            t = 0;
        }
        return t;
    }
    const closeModal = () => {
        p.s(null, p.t);
        setTimeout(() => {
            setType(null);
            updateInput(0);
        }, 250);
    }
    if(!p.t){
        return '';
    }
    if(p.t.ticker === 'RUBRUB'){
        return '';
    }
    const makeDeal = () => {
        let a = 1;
        let c;
        let y = p.c;
        let x = p.p;
        if(type === 'buy'){
            a = -1;
        }
        if(isCurency){
            //b = p.c.rub.count + input * a * p.t.price;
            c = p.t;
        }
        else{
            y.usd.count += input * a * p.t.price;
            y.usd.avgPrice = (p.c.usd.avgPrice * p.c.usd.count + input * a * p.t.price) / (p.c.usd.count + input * a);
            let i = 0;
            for (const v of p.p) {
                if(v.ticker === p.t.ticker){
                    c = {
                        ticker: v.ticker,
                        avgPrice: (v.avgPrice * v.count + input * a * -1 * p.t.price) / (v.count + input * a * -1),
                        count: v.count + input * a * -1
                    }
                    x[i] = c;
                    break;
                }
                i++;
            }
            if(!c){
                c = {
                    ticker: p.t.ticker,
                    avgPrice: p.t.price,
                    count: input * a * -1
                }
                x.push(c);
            }
            for(let i = 0; i < x.length; i++) {
                delete x[i].price;
                delete x[i].name;
                if(x[i].count === 0){
                    x.splice(i, 1);
                }
            }
        }
        if(!x){
            x = [];
        }
        fs.collection('users').doc('1').update({portfolio: x, currencies: p.c});
    }

    let isCurency = ['RUBCHF', 'RUBUSD', 'RUBEUR', 'RUBRUB'].indexOf(p.t.ticker) >= 0;

    return <ModalRoot activeModal={p.v} onClose={closeModal}>
        <ModalPage
            id={"stock"}
            header={<ModalPageHeader left={<PanelHeaderBack onClick={closeModal}/>}><Header>{p.t.name}</Header></ModalPageHeader>}
            dynamicContentHeight
        >
            <FormLayout>
                {type === null ?
                    <FormItem style={{display: 'flex'}}>
                        <Button onClick={() => {setType('buy')}} mode={"commerce"} stretched size={"l"}>Купить</Button>
                        <div style={{width: 10}}/>
                        <Button onClick={() => {setType('sell')}} mode={"destructive"} stretched size={"l"}>Продать</Button>
                    </FormItem>
                    :
                    <div>
                        <FormItem>
                            <Title level={1} weight={'bold'}>{type === 'buy' ? 'Покупка' : 'Продажа'}</Title>
                        </FormItem>
                        <FormItem>
                            <Input disabled value={parseFloat(p.t.price).toFixed(2)}/>
                        </FormItem>
                        <FormItem>
                            <Title level={1} weight={'bold'}>Количество</Title>
                        </FormItem>
                        <FormItem bottom={`доступно 
                            ${type === 'buy' ? isCurency ? 
                                parseInt(p.c.rub.count / p.t.price) : 
                                parseInt(p.c.usd.count / p.t.price) 
                            : p.t.count
                        }
                        шт.`}>
                            <Input after={
                                <div style={{display: 'flex'}}>
                                    {input !== 0 ? <IconButton icon={<Icon24Cancel onClick={() => updateInput(0)}/>}/> : ''}
                                    <IconButton icon={<Icon24Add onClick={() => {updateInput(input + 1)}}/>}/>
                                </div>
                            }
                                   onChange={(e) => updateInput(formatInput(e.target.value))}
                                   value={input}
                            />
                        </FormItem>
                        <FormItem>
                            <Button disabled={input === 0} onClick={makeDeal} size={"l"} stretched>{type === 'buy' ? 'Купить' : 'Продать'}</Button>
                        </FormItem>
                    </div>
                }
            </FormLayout>
        </ModalPage>
    </ModalRoot>
}

export default Modal;