import React from 'react'
import {CardScroll, Group, Header, Spinner} from "@vkontakte/vkui";
import BalanceCard from "./BalanceCard";

const BalanceCards = (p) => {
    if(p.exchangeRates.rub === 1){
        return <Spinner/>
    }
    // let CHFUSD = p.exchangeRates.chf / p.exchangeRates.usd;
    // let GBPUSD = p.exchangeRates.gbp / p.exchangeRates.usd;
    // let EURUSD = p.exchangeRates.eur / p.exchangeRates.usd;
    // let RUBUSD = p.exchangeRates.rub / p.exchangeRates.usd;
    let CHFUSD = p.exchangeRates.chf.val / p.exchangeRates.usd.val;
    let GBPUSD = p.exchangeRates.gbp.val / p.exchangeRates.usd.val;
    let EURUSD = p.exchangeRates.eur.val / p.exchangeRates.usd.val;
    let RUBUSD = p.exchangeRates.rub.val / p.exchangeRates.usd.val;
    let rates = {
        usd: 1,
        chf: CHFUSD,
        gbp: GBPUSD,
        eur: EURUSD,
        rub: RUBUSD
    }
    let usdValue = 0;
    p.portfolio.forEach(v => {
        usdValue += v.price * v.count;
    });
    for (const [k, v] of Object.entries(p.cash)) {
        usdValue += v.count * rates[k];
    }
    let eurValue = usdValue / EURUSD;
    let rubValue = usdValue / RUBUSD;

    return <Group header={<Header mode={"secondary"}>Счет</Header>}>
        <CardScroll size={"s"}>
            <BalanceCard a={'rub'} b={rubValue}/>
            <BalanceCard a={'usd'} b={usdValue}/>
            <BalanceCard a={'eur'} b={eurValue}/>
        </CardScroll>
    </Group>
}

export default BalanceCards;