import React from "react";
import {Avatar, Caption, Cell, Title} from "@vkontakte/vkui";
import StocksData from "../Functions/StocksData";

const StockCard = (p) => {
    let c = p.c.toFixed(2);
    let v = p.v;
    let a = p.a;
    let b = p.b;
    let z = p.z;
    let d = StocksData[v];
    let x = ((c / a) * 100 - 100).toFixed(2);
    let y = c - a;
    return <Cell
        onClick={() => {p.s('stock', {
            ticker: v,
            name: d.name,
            price: c,
            count: b,
            avgPrice: a
        })}}
        before={
            <Avatar
                size={32}
                src={require(`../Logos/${d.logo}`).default}
            />}
        description={p.p ?
            <Caption
                style={v === 'RUBRUB' ? {display: 'none'} : {}}
                level={2}
                weight={"regular"}>
                {`${b.toFixed(2)} ${p.o} · ${c} ${z}`}
            </Caption>
            : ''}
        indicator={
            <div style={{textAlign: "right"}}>
            <Title
                style={{color: 'var(--header_text)'}}
                level={3}
                weight={"medium"}>
                {p.p ? (b*c).toFixed(2) : c} {z}
            </Title>
            <Caption
                style={
                    v === 'RUBRUB' ? {display: 'none'} :
                        x >= 0 ?
                            {color: 'var(--dynamic_green)'} :
                            {color: 'var(--dynamic_red)'}
                }
                level={2}
                weight={"regular"}>
                {(b*y).toFixed(2)} {z} ({x}%)
            </Caption>
        </div>}
    >
        <Title level={3} weight={"semibold"}>{d.name}</Title>
    </Cell>
}

export default StockCard;